<template>
  <div class="docFaTongzhi">
    <div class="xuanze-patient float-box" >
      发送至：
      <span class="patient-name">晚晴，金口难开，</span>
      <span class="patient-num">10人</span>
      <span class="to-xuanze fr  iconfont icon-jiahao"  @click="toChoose"></span>
    </div>
    <div class="beizhu-box">
      <textarea name="" v-model="textNumber" id="disease-status" @onkeyup="countLength()" maxlength="150" class="zhengzhuang" placeholder="请输入备注信息">
      </textarea>
      <div class="beizhu-num" >
        <span class='two'><span id="textNum" >0</span>/500</span>
      </div>
      <button class="button-box" :class="{isButton: active}" >发送</button>
    </div>
  </div>
</template>
<script>
  export default {
    name: 'docFaTongzhi',
    data () {
      return {
        textNumber:'',
        active:false
      }
    },
    created() {
      document.getElementsByTagName('title')[0].innerHTML = '发送通知'
    },
    watch: {
      'textNumber' () {
        var a=this.textNumber.length;
        document.getElementById('textNum').innerHTML = a;
        if(this.textNumber){
          this.active = true
        }else{
          this.active = false
        }
      }
    },
    methods: {
      toChoose() {
          console.log(123)
        this.$router.push({path:'/baochuan_d/docChoosePatient'})
      }
    }
  }
</script>

<style lang="scss">
.docFaTongzhi{
  min-height:100vh;
  background: #FCFCFC;
  width:100%;
  padding-top:15px;
  .xuanze-patient{
    width:85%;
    margin:16px auto;
    padding:5px;
    background:#fff;
    border-radius:5px;
    -webkit-box-shadow:0 0 5px 5px #E5EBED;
    -moz-box-shadow:0 0 5px 5px #E5EBED;
    box-shadow:0 0 5px 5px #E5EBED;
    font-size:16px;
    color:#232323;
    line-height:38px;
    .patient-name,.patient-num{
      font-size:14px;
      color:#bbb;
    }
    .icon-jiahao{
      font-size:28px;
      color:#bbb;
    }
  }
  .beizhu-box{
    width:90%;
    margin:30px auto;
    .zhengzhuang{
      width: 100%;
      height: 200px;
      border: 1px solid #dedede;
      border-radius: 5px;
      text-indent: 5px;
      padding: 5px;
      line-height: 20px;
      font-size: 14px;
      color: #666;
      outline: none;
      box-sizing: border-box;
      -webkit-appearance: none;
      -webkit-box-shadow:0 0 5px 5px #E5EBED;
      -moz-box-shadow:0 0 5px 5px #E5EBED;
      box-shadow:0 0 5px 5px #E5EBED;
    }
    .beizhu-num{
      position:absolute;
      top:280px;
      left:75%;
      color:#bbb;
      font-size:15px;
    }
    .button-box{
      position:fixed;
      bottom:25px;
      display: block;
      width: 90%;
      height:45px;
      margin:auto;
      background:#86B8B8;
      color: #ffffff;
      border:1px solid #86B8B8;
      border-radius:22px;
      outline:none;
      font-size:18px;
    }
    .button-box.isButton {
      background:#529D98;
      border:1px solid #f4f4f4;
      -webkit-box-shadow:0 0 10px #f4f4f4;
      -moz-box-shadow:0 0 10px #f4f4f4;
      box-shadow:0 0 10px #f4f4f4;
    }
  }
}
</style>



